import React, {useState} from 'react';
import {Avatar, AvatarBadge, Stack, useDisclosure} from '@chakra-ui/react'

import {
    Box,
    Text,
    Flex
} from '@chakra-ui/react';

import {
    Image
} from '@chakra-ui/react';

export default function MyProfile(props){
    return(
        <>
            <Box as='button' w='full' h='22px' borderTop='0px' borderBottom='1px' borderColor='#F7F7F7' fontSize={0} lineHeight={0} >
                <Text fontSize='xs' float='left' fontWeight='bold' mt='5px' ml='10px' mb='5px' color='#858585'>
                    내 프로필
                </Text>
            </Box>
            <Box color='white' mt='0' mb='0' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' bg='white'>
                <Flex as='button' w='full' color='white' borderTop='1px' borderBottom='1px' borderColor='#F7F7F7' >
                    <Box marginTop='5px' marginBottom='5px' width='50px'>
                        {/*<Image
                            borderRadius='full'
                            boxSize='40px'
                            ml='10px'
                            src='https://bit.ly/dan-abramov'
                            alt='Dan Abramov'
                        />*/}
                        <Stack direction='row' spacing={4}>
                            <Avatar ml='10px' boxSize='2em' src='https://bit.ly/dan-abramov'>
                                <AvatarBadge boxSize='1em' bg='yellow.500' />
                            </Avatar>
                        </Stack>
                    </Box>
                    <Box flex='1' padding='5px' textAlign='left'>
                        <Text fontSize='15px' fontWeight='bold' color='black' marginLeft='5px'>테스트 주임</Text>
                        <Text fontSize='11px' color='gray.500' fontWeight='bold' marginLeft='5px'>개발 2팀 / 회사이름</Text>
                    </Box>
                    <Box alignItems='center' width='50px'>

                    </Box>
                </Flex>
            </Box>
        </>
    );
}